/* src/styles/Resume.css */
.resume-container {
  padding: 40px 20px;
  max-width: 900px;
  margin: 0 auto;
  text-align: center;
  color: #f1f1f1;
}

.section-title {
  font-size: 32px;
  margin-bottom: 20px;
  color: #00ffff;
}

.resume-text {
  font-size: 16px;
  margin-bottom: 24px;
  color: #cccccc;
}

.download-button {
  display: inline-block;
  padding: 12px 24px;
  background-color: #00ffff30;
  color: #00ffff;
  border: 1px solid #00ffff50;
  border-radius: 10px;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease;
}

.download-button:hover {
  background-color: #00ffff55;
  color: #000;
}

.quick-highlights {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  flex-wrap: wrap;
}

.highlight-box {
  background-color: #1f1f1f;
  border: 1px solid #00ffff25;
  padding: 16px;
  border-radius: 12px;
  max-width: 260px;
  flex: 1 1 auto;
}

.highlight-box h4 {
  font-size: 16px;
  margin-bottom: 8px;
  color: #00ffff;
}

.highlight-box p {
  font-size: 14px;
  color: #dddddd;
}
