/* src/index.css */

:root {
  --bg-color: #ffffff;
  --text-color: #0f172a;
  --card-bg-color: #f1f5f9;
  --accent-color: #0ea5e9;
  --accent-color-hover: #0284c7;
  --button-text-color: #ffffff;
  --input-bg-color: #e2e8f0;
  --input-border-color: #94a3b8;
  --hover-bg-color: rgba(14, 165, 233, 0.05);
  --muted-text-color: #475569;
  --border-color: #e2e8f0;
  --card-shadow: rgba(0, 0, 0, 0.1);
}

