/* src/styles/Contact.css */
.contact-section {
  padding: 50px 20px;
  max-width: 1000px;
  margin: 0 auto;
  color: #f1f1f1;
  text-align: center;
}

.section-title {
  font-size: 32px;
  color: #00ffff;
  margin-bottom: 30px;
}

.contact-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
}

.contact-left {
  flex: 1;
}

.contact-img {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 3px solid #00ffff90;
  object-fit: cover;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.2);
}

.contact-right {
  flex: 2;
  font-size: 16px;
}

.contact-right p {
  margin: 10px 0;
}

.contact-right a {
  color: #00ffff;
  text-decoration: underline;
}

.contact-links {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
}

.contact-links a {
  font-weight: bold;
  color: #00ffff;
  background-color: #1e1e1e;
  padding: 10px 16px;
  border-radius: 8px;
  border: 1px solid #00ffff40;
  transition: all 0.3s ease;
}

.contact-links a:hover {
  background-color: #00ffff20;
  color: #000;
}

.footer {
  margin-top: 50px;
  font-size: 14px;
  color: #aaa;
}
