/* src/styles/Projects.css */
.projects-section {
  padding: 40px 20px;
  max-width: 1100px;
  margin: 0 auto;
  color: #f1f1f1;
}

.section-title {
  text-align: center;
  font-size: 32px;
  color: #00ffff;
  margin-bottom: 20px;
}

.tabs {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 12px;
  margin-bottom: 30px;
}

.tab-btn {
  background: transparent;
  border: 1px solid #00ffff70;
  padding: 10px 18px;
  border-radius: 20px;
  color: #00ffff;
  cursor: pointer;
  transition: all 0.3s ease;
}

.tab-btn:hover,
.tab-btn.active {
  background-color: #00ffff30;
  color: #000;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

.project-card {
  background-color: #1f1f1f;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid #00ffff20;
  transition: transform 0.2s ease;
}

.project-card:hover {
  transform: translateY(-5px);
}

.project-category {
  color: #00ffff80;
  font-size: 14px;
  margin-bottom: 10px;
}

.tech-used {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 10px 0;
  font-size: 13px;
  color: #cccccc;
  list-style: none;
  padding: 0;
}

.links a {
  margin-right: 10px;
  color: #00ffff;
  font-size: 14px;
  text-decoration: underline;
}
