/* src/styles/Home.css */
.home-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
}

.card {
  background-color: #1e1e1e;
  padding: 30px;
  border-radius: 16px;
  text-align: center;
  max-width: 500px;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 255, 255, 0.1);
  transition: transform 0.3s;
}

.card:hover {
  transform: translateY(-5px);
}

.profile-pic {
  width: 160px;
  height: 160px;
  object-fit: cover;
  border-radius: 50%;
  margin-bottom: 20px;
  border: 3px solid #00ffff;
}

.name {
  font-size: 28px;
  margin-bottom: 4px;
  color: #ffffff;
}

.role {
  font-size: 18px;
  color: #00ffff;
  margin-bottom: 16px;
}

.intro {
  font-size: 15px;
  color: #dddddd;
  margin-bottom: 20px;
}

.contact-info p {
  margin: 8px 0;
  font-size: 14px;
}

.contact-info a {
  color: #00ffff;
  text-decoration: none;
}
