/* src/styles/Navbar.css */
.navbar {
  width: 100%;
  background-color: #1f1f1f;
  padding: 12px 24px;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  z-index: 1000;
}

.nav-list {
  display: flex;
  list-style: none;
  gap: 20px;
  flex-wrap: wrap;
}

.nav-item {
  color: #ccc;
  cursor: pointer;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-item:hover,
.nav-item.active {
  color: #00ffff;
  border-bottom: 2px solid #00ffff;
}
