/* src/styles/Skills.css */
.skills-container {
  padding: 40px 20px;
  max-width: 1000px;
  margin: 0 auto;
  color: #f1f1f1;
}

.section-title {
  font-size: 32px;
  text-align: center;
  margin-bottom: 32px;
  color: #00ffff;
}

.skills-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 20px;
}

.skill-block {
  background-color: #1e1e1e;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid #00ffff30;
  box-shadow: 0 0 8px rgba(0, 255, 255, 0.08);
}

.skill-category {
  font-size: 18px;
  margin-bottom: 16px;
  color: #00ffff;
  border-bottom: 1px solid #00ffff40;
  padding-bottom: 8px;
}

.skill-list {
  list-style: none;
  padding: 0;
}

.skill-item {
  font-size: 14px;
  margin-bottom: 8px;
  color: #cccccc;
}
